import React from 'react';
import { default as MuiFormControl, FormControlProps } from '@mui/material/FormControl';

const FormControl = React.forwardRef<HTMLElement, FormControlProps>( ( props, ref ) => {
	return (
		<MuiFormControl
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<FormControlProps>;

export default FormControl;
